import { Component, OnInit } from '@angular/core';
import { AuthService } from 'src/app/services/auth.service';
import { Router } from '@angular/router';
import { AlertController } from '@ionic/angular';
interface User {
  email: string
  password: string
}
@Component({
  selector: 'app-sign-in',
  templateUrl: './sign-in.page.html',
  styleUrls: ['./sign-in.page.scss'],
})
export class SignInPage implements OnInit {
  user: User = {
    email: "",
    password: ""
  }
  constructor(private auth: AuthService,
    public alertController: AlertController,
    private router: Router,) { }

  ngOnInit() {
  }
  async presentAlert(msg: string, header?: string, callback?: any) {
    const alert = await this.alertController.create({
      cssClass: 'alert-box',
      header: header,
      message: msg,
      buttons: [{
        text: 'OK', handler: () => {
          if (callback != undefined) callback()
        }
      }],
    });
    await alert.present();
  }
  authMail() {
    this.auth.SignIn(this.user.email, this.user.password).then(data => {
      console.log(data)
      if (data.user && data.user.emailVerified !== true) this.presentAlert("Please validate your email address. Kindly check your inbox.", "You successfully registered", () => this.router.navigateByUrl("/sign-in"))
      else if (data.message) this.presentAlert(data.message)

    })
  }
  authGithub() { }
  authGoogle() {
    this.auth.GoogleAuth()
  }
}
